// Color system
$white:    #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #6c757d !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #151e28 !default;

$black: #000 !default;
$red:     #dc3545 !default;
$orange:  #fd7e14 !default;
$yellow:  #ffc107 !default;
$green:   #198754 !default;
$teal:    #20c997 !default;
$cyan:    #0dcaf0 !default;
$primary: #624BFF !default;

$secondary:     $gray-600 !default;
$success:       $green !default;
$info:          $cyan !default;
$warning:       $yellow !default;
$danger:        $red !default;
$light:         $gray-100 !default;
$dark:          $gray-900 !default;
$light: $gray-100 !default;
$dark: $gray-900 !default;
$light-cornflower: #f2f3b1 !default;
$pink-100: #f7d6e6 !default;

// Light color
$light-primary: #e3dcff !default; // NEW
$light-secondary: #e4e3e9 !default; // NEW
$light-success: #d1f5ea !default; // NEW
$light-danger: #fad9d8 !default; // NEW
$light-warning: #ffeeda !default; // NEW
$light-info: #e1f6ff !default; // NEW
$light-dark: #d1cfd8 !default; // NEW
$light-pink: $pink-100 !default; // NEW
$light-cornflower: #f2f3b1 !default; // NEW

// Dark color
$dark-primary: #593cc1 !default; // NEW
$dark-secondary: #5c596d !default; // NEW
$dark-success: #139a74 !default; // NEW
$dark-danger: #ae302e !default; // NEW
$dark-warning: #c28135 !default; // NEW
$dark-info: #51a0c2 !default; // NEW
$white: $white !default;
$white-20: rgba($white, 0.2) !default;
$theme-colors: (
    "primary": $primary,
    "secondary": $secondary,
    "success": $success,
    "info": $info,
    "warning": $warning,
    "danger": $danger,
    "light": $light,
    "dark": $dark,
    "white": $white,
    "light-primary": $light-primary,
    "light-danger": $light-danger,
    "light-info": $light-info,
    "light-warning": $light-warning,
    "light-secondary": $light-secondary,
    "light-success": $light-success,
    "light-pink": $light-pink,
    "light-cornflower": $light-cornflower,
    "light-dark": $light-dark,
    "dark-primary": $dark-primary,
    "dark-warning": $dark-warning,
    "dark-info": $dark-info,
    "dark-danger": $dark-danger,
    "dark-success": $dark-success,
    "dark-secondary": $dark-secondary,
);


// The contrast ratio to reach against white, to determine if color changes from "light" to "dark". Acceptable values for WCAG 2.0 are 3, 4.5 and 7.
// See https://www.w3.org/TR/WCAG20/#visual-audio-contrast-contrast
$min-contrast-ratio: 2 !default;
$body-bg: $gray-100 !default;
$body-color: $gray-600 !default;


// Typography
// Font, line-height, and color for body text, headings, and more.
// stylelint-disable value-keyword-case
$font-family-sans-serif: "Inter", "sans-serif" !default;
$font-family-base: $font-family-sans-serif !default;
$font-family-heading: $font-family-sans-serif;
$font-size-base: 1rem !default;


// Font Awesome
$font-awesome: "Font Awesome 5 Free";


// Font Weight
$font-weight-thin: 100 !default;
$font-weight-lighter: 200 !default;
$font-weight-light: 300 !default;
$font-weight-normal: 400 !default;
$font-weight-medium: 500 !default;
$font-weight-semi-bold: 600 !default;
$font-weight-bold: 700 !default;
$font-weight-bolder: 800 !default;
$font-weight-base: $font-weight-normal !default;


// Line height
$line-height-base: 1.6 !default;
$line-height-1: 1 !default;
$line-height-2: 2 !default;
$line-height-sm: 1.2 !default;
$line-height-md: 1.6 !default;
$line-height-lg: 1.8 !default;


// Text Muted
$text-muted: $gray-500 !default;


// Responsive Font Size
$enable-rfs:                  true !default;

// Typography
$h1-font-size: 2rem !default;
$h2-font-size: 1.5rem !default;
$h3-font-size: 1.25rem !default;
$h4-font-size: 1rem !default;
$h5-font-size: 0.875rem !default;
$h6-font-size: 0.75rem !default;
$headings-font-family: $font-family-heading !default;
// scss-docs-start font-sizes
$font-sizes: (
    1: 3rem,
    2: 2.5rem,
    3: 1.25rem,
    4: 1rem,
    5: 0.875rem,
    6: 0.75rem,
    7: 0.625rem,
) !default;
// scss-docs-end font-sizes
$headings-font-weight: 500 !default;
$headings-line-height: 1.4 !default;
$headings-color: $gray-900 !default;
$link-hover-decoration: none !default;
// scss-docs-start display-headings
$display-font-sizes: (
    1: 4.5rem,
    2: 3.5rem,
    3: 3rem,
    4: 2.5rem,
    5: 2rem,
    6: 1rem,
) !default;
$display-font-weight: 600 !default;
$display-line-height: 1.3 !default;

// scss-docs-end display-headings
$lead-font-size: 1.25rem !default;
$lead-font-weight: 400 !default;


// grid
$grid-gutter-width: 32px !default;

// Breadcrumb
$breadcrumb-font-size: 12px;

// Border
$border-width: 1px !default;
$border-color: $gray-200 !default;
$border-radius-md: 0.5rem !default;
$border-radius: 0.25rem !default;
$border-radius-lg: 0.5rem !default;
$border-radius-sm: 0.2rem !default;


// border top left rounded
$border-top-left-radius-md: 0.5rem !default;
$border-top-left-radius: 0.25rem !default;
$border-top-left-radius-lg: 0.5rem !default;
$border-top-left-radius-sm: 0.2rem !default;
$border-top-left-radius-xl: 2rem !default;
$border-top-left-radius-xxl: 3rem !default;


// border top right rounded
$border-top-right-radius-md: 0.5rem !default;
$border-top-right-radius: 0.25rem !default;
$border-top-right-radius-lg: 0.5rem !default;
$border-top-right-radius-sm: 0.2rem !default;
$border-top-right-radius-xl: 2rem !default;
$border-top-right-radius-xxl: 3rem !default;


// border bottom left rounded
$border-bottom-left-radius-md: 0.5rem !default;
$border-bottom-left-radius: 0.25rem !default;
$border-bottom-left-radius-lg: 0.5rem !default;
$border-bottom-left-radius-sm: 0.2rem !default;
$border-bottom-left-radius-xl: 2rem !default;
$border-bottom-left-radius-xxl: 3rem !default;


// border bottom right rounded
$border-bottom-right-radius-md: 0.5rem !default;
$border-bottom-right-radius: 0.25rem !default;
$border-bottom-right-radius-lg: 0.5rem !default;
$border-bottom-right-radius-sm: 0.2rem !default;
$border-bottom-right-radius-xl: 2rem !default;
$border-bottom-right-radius-xxl: 3rem !default;


// Smooth Box Shadow
$box-shadow-smooth-sm: 0px 2px 4px rgba(0, 0, 20, 0.08),0px 1px 2px rgba(0, 0, 20, 0.08)!default;
$box-shadow-smooth-md: 0 1px 2px rgba(21,30,40,0.07),0 2px 4px rgba(21,30,40,0.07),0 4px 8px rgba(21,30,40,0.07),0 8px 16px rgba(21,30,40,0.07),0 16px 32px rgba(21,30,40,0.07),0 32px 64px rgba(21,30,40,0.07)!default;
$box-shadow-smooth-lg: 0 1px 1px rgba(21,30,40,0.11),0 2px 2px rgba(21,30,40,0.11),0 4px 4px rgba(21,30,40,0.11),0 8px 8px rgba(21,30,40,0.11),0 16px 16px rgba(21,30,40,0.11),0 32px 32px rgba(21,30,40,0.11)!default;

//
$nav-link-color:                    $gray-600;

// Input
$input-btn-padding-y: 0.75rem !default;
$input-btn-padding-x: 1.25rem !default;
$input-btn-font-family: null !default;
$input-btn-font-size: 0.875rem !default;
$input-btn-padding-y-sm: 0.5rem !default;
$input-btn-padding-x-sm: 0.875rem !default;
$input-border-color: $gray-300 !default;
$input-bg: $white !default;
$input-color: $gray-900 !default;
$input-placeholder-color: $gray-500 !default;
$input-btn-padding-y-lg: 0.75rem !default;
$input-btn-padding-x-lg: 1.25rem !default;
$input-bg: $white !default;
$input-btn-line-height: $line-height-base !default;
$input-focus-bg: $white !default;
$input-disabled-bg: $gray-100 !default;
$input-focus-border-color: $primary !default;
$input-focus-box-shadow: 0px 0px 0px 3px rgba(231, 225, 253, 0.75) !default;
$input-group-addon-bg: $white !default;
$input-border-radius: $border-radius !default;
$custom-file-color: $gray-700 !default;
$input-font-size: $h5-font-size !default;
$input-btn-padding-y-xs: 0.22rem !default;
$input-btn-padding-x-xs: 0.75rem !default;
$input-btn-font-size-xs: 0.75rem !default;


// Button
$btn-font-weight: 600 !default;
$btn-border-radius: $border-radius !default;


// Badges
$badge-font-weight: 500;
$badge-padding-y: 0.33rem !default;
$badge-padding-x: 0.5rem !default;


// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.
// scss-docs-start container-max-widths
$container-max-widths: (
    sm: 540px,
    md: 720px,
    lg: 960px,
    xl: 1140px // xxl: 1320px,
) !default;
// scss-docs-end container-max-widths
@include _assert-ascending($container-max-widths, "$container-max-widths");


// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.
// stylelint-disable-next-line scss/dollar-variable-default
$spacer: 1rem !default;
$spacers: (
    0: 0,
    1: $spacer * 0.25,
    // 4px
    2: $spacer * 0.5,
    // 8px
    3: $spacer * 1,
    // 16px
    4: $spacer * 1.5,
    // 24px
    5: $spacer * 2,
    // 32px
    6: $spacer * 2.5,
    // 40px
    7: $spacer * 3,
    // 48px
    8: $spacer * 3.5,
    // 56px
    9: $spacer * 4,
    // 64px
    10: $spacer * 4.5,
    // 72px
    11: $spacer * 5,
    // 80px
    12: $spacer * 5.5,
    // 88px
    13: $spacer * 6,
    // 96px
    14: $spacer * 6.5,
    // 104px
    15: $spacer * 7,
    // 112px
    16: $spacer * 7.5,
    // 120px
    17: $spacer * 8,
    // 128px
    18: $spacer * 8.5,
    // 136px
    19: $spacer * 9,
    // 144px
    20: $spacer * 9.5,
    // 152px
    21: $spacer * 10,
    // 160px
    22: $spacer * 12,
    // 288px
    23: $spacer * 13,
    // 288px
) !default;


$enable-negative-margins: true !default;



// Card
$card-spacer-y: $spacer * 1.5 !default;
$card-spacer-x: $spacer * 1.5 !default;
$card-cap-padding-y: $card-spacer-y / 1.5 !default;
$card-cap-padding-x: $card-spacer-x !default;
$card-border-radius: $border-radius-md !default;
$card-border-color: $gray-200;
$card-cap-bg: $white !default;


// list group
$list-group-item-padding-y: $spacer * 0.75 !default;
$list-group-item-padding-x: $spacer * 1.25 !default;
$list-group-border-color: $gray-200 !default;


// Box Shadow
$box-shadow-sm: 0px 1px 3px rgba(3, 0, 71, 0.09) !default;
$box-shadow: 0px 3px 6px rgba(3, 0, 71, 0.1) !default;
$box-shadow-lg: 0 6px 8px 0 rgba(0, 0, 0, 0.08), 0 0 0 1px rgba(0, 0, 0, 0.04) !default;


// Smooth Box Shadow
$box-shadow-smooth-sm: 0px 2px 4px rgba(0, 0, 20, 0.08), 0px 1px 2px rgba(0, 0, 20, 0.08) !default;
$box-shadow-smooth-md: 0 1px 2px rgba(21, 30, 40, 0.07), 0 2px 4px rgba(21, 30, 40, 0.07),
    0 4px 8px rgba(21, 30, 40, 0.07), 0 8px 16px rgba(21, 30, 40, 0.07), 0 16px 32px rgba(21, 30, 40, 0.07),
    0 32px 64px rgba(21, 30, 40, 0.07) !default;
$box-shadow-smooth-lg: 0 1px 1px rgba(21, 30, 40, 0.11), 0 2px 2px rgba(21, 30, 40, 0.11),
    0 4px 4px rgba(21, 30, 40, 0.11), 0 8px 8px rgba(21, 30, 40, 0.11), 0 16px 16px rgba(21, 30, 40, 0.11),
    0 32px 32px rgba(21, 30, 40, 0.11) !default;


// Hr
$hr-color: $gray-200 !default;
$hr-height: $border-width !default;
$hr-opacity: 1 !default;


// Pagination
$pagination-padding-y: 0.5rem !default;
$pagination-padding-x: 0.925rem !default;
$pagination-padding-y-sm: 0.25rem !default;
$pagination-padding-x-sm: 0.5rem !default;
$pagination-padding-y-lg: 0.75rem !default;
$pagination-padding-x-lg: 1.5rem !default;
$pagination-line-height: 1.25 !default;
$pagination-color: $gray-700 !default;
$pagination-bg: $white !default;
$pagination-border-width: $border-width !default;
$pagination-border-color: $gray-200 !default;
$pagination-hover-color: $white !default;
$pagination-hover-bg: $primary !default;
$pagination-hover-border-color: $primary !default;


// Modal
$modal-header-padding-y: 1rem !default;
$modal-header-padding-x: 1.5rem !default;


// Padding applied to the modal body
$modal-inner-padding: 1.5rem !default;


// Avatar
$avatar-size-xs: 1.5rem !default;
$avatar-size-sm: 2rem !default;
$avatar-size-md: 2.5rem !default;
$avatar-size-lg: 3.5rem !default;
$avatar-size-xl: 5rem !default;
$avatar-size-xxl: 7.5rem !default;


// Icon
$icon-size-xs: 1.5rem !default;
$icon-size-sm: 2rem !default;
$icon-size-md: 2.5rem !default;
$icon-size-lg: 3rem !default;
$icon-size-xl: 3.5rem !default;
$icon-size-xxl: 4rem !default;
$icon-size-xxxl: 6rem !default;


// Images aspect ratio 4:3
$img-4by3-xxl: 9rem;
$img-4by3-xl: 7.75rem;
$img-4by3-lg: 6.5rem;
$img-4by3-md: 5.25rem;
$img-4by3-sm: 4rem;
$img-4by3-xs: 2.75rem;


// Social buttons
$social-btn-transition: border-color 0.25s ease-in-out, background-color 0.25s ease-in-out, color 0.25s ease-in-out !default;
$social-btn-size: 2.25rem !default;
$social-btn-size-sm: 1.75rem !default;
$social-btn-size-lg: 2.75rem !default;


// Close
$close-font-size: 2rem !default;
$close-font-weight: $font-weight-bold !default;
$close-color: $gray-600 !default;
$close-text-shadow: 0 1px 0 $white !default;


// letter spacing
$letter-spacing-sm: -0.04em;
$letter-spacing-xs: -0.08em;
$letter-spacing-base: 0em;
$letter-spacing-md: 0.1em;
$letter-spacing-lg: 0.15em;
$letter-spacing-xl: 0.2em;
$letter-spacing-xxl: 0.25em;


// Tables
//
// Customizes the `.table` component with basic values, each used across all table variations.


// scss-docs-start table-loop
$table-variants: (
  "white":       $white,
) !default;




// Table extend from bootstrap
$table-cell-padding-y:        .5rem;
$table-cell-padding-x:        .5rem;
$table-cell-padding-y-sm:     .25rem;
$table-cell-padding-x-sm:     .25rem;
$table-head-color: $gray-700 !default;
$table-th-font-weight:        600;
$table-accent-bg: transparent !default;
$table-hover-bg: rgba($gray-100, 0.7) !default;

// table striped
$table-striped-bg-factor: 1 !default;
$table-striped-bg: rgba($gray-100, $table-striped-bg-factor) !default;

// table active
$table-active-bg-factor: 1 !default;
$table-active-bg: rgba($gray-100, $table-active-bg-factor) !default;
$table-group-separator-color: $gray-200 !default;

// link decoration
$link-decoration: none !default;
// Accordion
// scss-docs-start accordion-variables
$accordion-bg: $white !default;
$accordion-border-color: $gray-200 !default;
